<template>
  <div class="login-view">
    <el-form :rules="rules" ref="loginForm" :model="form" label-width="80px">
      <el-form-item label-width="0" prop="username">
        <el-input
          placeholder="用户名"
          clearable
          v-model="form.username"
        ></el-input>
      </el-form-item>

      <el-form-item label-width="0" v-show="status === 1" prop="nickname">
        <el-input
          placeholder="昵称"
          clearable
          v-model="form.nickname"
        ></el-input>
      </el-form-item>

      <el-form-item label-width="0" prop="password">
        <el-input
          type="password"
          placeholder="密码"
          show-password
          clearable
          v-model="form.password"
        ></el-input>
      </el-form-item>
        <el-button type="primary" @click="onSubmit" class="login-button">立即{{ title }}</el-button>
        <br />
        <el-button type="primary" @click="toggleStatue" class="switch-btn">{{ switchLabel }}</el-button>
    </el-form>
    <Alert v-if="show" @click="gogoalert" :count="count"/>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Alert from "./Alert.vue"
// 用选择登录
const LOGIN = 0;
// 用选择注册
const REGISTER = 1;

export default {
  components:{
      Alert
  },
  data() {
    return {
      form: {
        username: "",
        password: "",
        nickname: "",
      },
      status: LOGIN,
      show:false,
      count:3
    };
  },
  computed: {
    // 当前表单验证规则
    rules() {
      return {
        username: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 5, max: 11, message: "长度在 5 到 11 个字符", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        nickname: [
          {
            required: this.status === LOGIN ? false : true,
            message: "请选输入昵称",
            trigger: "blur",
          },
        ],
      };
    },
    title() {
      if (this.status === LOGIN) {
        return "登录";
      }
      return "注册";
    },
    switchLabel() {
      if (this.status === LOGIN) {
        return "免费注册";
      }
      return "去登录";
    },
  },
  methods: {
     gogoalert(){
          this.status = LOGIN
          this.show = false
    },
    ...mapActions(["onRegisterUser", "onLoginUser"]),
    
    toggleStatue() {
      if (this.status === LOGIN) {
        this.status = REGISTER;
      } else {
        this.status = LOGIN;
      }
    },
    onSubmit() {
      // 先做表单验证
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // 当所有表单符合要求时进入该判断语句
          if(this.status === REGISTER) {
              this.onRegisterUser(this.form)
              .then(res => {
                  console.log('成功',res)
                  this.show = true 
                  let self = this
                  clearInterval()
                  setInterval(()=>{
                      self.count--
                      if(self.count===0){
                          self.status = LOGIN
                          self.show = false
                      }
                  },1000)
                  if(this.count<0){
                    clearInterval()
                    this.count=3
                  }
                   })
              .catch(err => console.log('失败',err))
          }
          if(this.status === LOGIN) {
              this.onLoginUser(this.form)
              .then(this.loginSuccess)
              .catch(err => console.log('登录失败', err))  
          }
        } else {
          console.log("请检查账号与密码格式是否输入正确");
          return false;
        }
      });
    },

    loginSuccess(token) {
        // 登陆成功后
        // 将 用户名 与 token 保存到 sessionStorage 中,实现自动登录
        let userInfo = {
            username: this.form.username,
            token,
        }
        sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
    },
  },
};
</script>

<style scoped>
.login-view {
  position:fixed;
  top: 40px;
  padding: 15px 20px;
  width:100%;
  height: 100%;
  background-color: #eee;
}

.switch-btn {
  margin-top: 5%;
  width: 100%;
  background-color: #fff;
  border: 1px solid #fecb64;
  color: #fecb64 ;
}
.login-button{
  width: 100%;
   border: 1px solid #fb3;
  background-color: #fb3;
  color: #fff;
}
</style>